footer {
    background: url("../img/footer_bg.jpg") no-repeat bottom;
    -webkit-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
 }
 
 .contact {
    width: 100%;
    min-height: 100vh;
    padding-top: 100px;
 }
 
 .contact__text {
    margin: 0 auto;
    text-align: center;
    margin-bottom: 50px;
    max-width: 360px;
 }
 
 .contact__title {
    font-size: 90px;
    font-weight: 700;
    margin-bottom: 70px;
    text-align: center;
 }
 
 .contact__desc {
    font-size: 17px;
    font-weight: 300;
 }
 
 .contact__frame {
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
 }
 
 .contact__client {
    max-width: 500px;
    width: 100%;
 }
 
 .contact__client--field {
    background: rgba(0, 196, 196, .5);
    line-height: 50px;
    border: 0;
    outline: 0;
    font-size: 15px;
    font-weight: 300;
    margin-bottom: 15px;
    padding-left: 20px;
    width: 100%;
 }
 
 .contact__client--field:active,
 .contact__client--field:hover{
    border-left: 5px solid #355b82;
 }
 
 .contact__client--field::placeholder {
    color: #fff;
 }
 
 .contact__client__meassage {
    line-height: 20px;
 }
 
 .contact__type {
    max-width: 500px;
    width: 100%;
    height: 100%;
 }
 
 .contact__type--name {
    font-size: 17px;
    font-weight: 700;
    text-transform: uppercase;
 }
 
 .contact__type--desc {
    font-size: 20px;
    margin-bottom: 9px;
 }
 
 .copyright {
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    -webkit-align-items: center;
    align-items: center;
    color: #b9b9b9;
    background: #000;
    height: 90px;
    padding: 0 19%;
 }
 .copyright__right {
    font-size: 16px;
    font-weight: 300;
 }
 
 .copyright__logo--minilogo {
    font-size: 25px;
    color: #b9b9b9;
    font-family: $pacifico;
    text-decoration: none;
 }
 
 .copyright__author {
    font-size: 15px;
    font-weight: 300;
 }
 
 .copyright__love {
    color: #f12b55;
    font-weight: 700;
 }